:root {
  --sky: #caebf2;
  --carbon: #a9a9a9;
  --watermelon: #ff3b3f;
  --neutral: #efefef;
  --almostWhite: #fafafa;
  --fresh: #4abdac;
  --niceGrey: #edeeef;
  --darkGrey: #222222;
  --whiteFont: #dcdcdc;
  --hoverGrey: #3c3c3c;
  --font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Open Sans", "Helvetica Neue", "Ubuntu", sans-serif;

  --linkHover: #ffffff;

  --headerHeight: 50px;
  --footerHeight: 350px;
  --tabHeight: 27px;

  --backgroundOdd: var(--niceGrey);
  --backgroundEven: white;
  --backgroundEducation: lighten($darkGrey, 5%);
  /* TODO: redo the main text color */
  --mainText: #5d5d5d;
  --mainLighterText: #5d5d5d;
  --mainLightestText: #5d5d5d;
  --subText: var(--carbon);
  --phoneMinWidth: 320px;
  --phoneMaxWidth: 479px;
  --padMinWidth: 480px;
  --padMaxWidth: 767px;
  --desktopMinWidth: 768px;
}

/* Todo: fix postcss custom media and use this */

.profileTile {
  display: flex;
  align-items: center;
  height: 75px;
}

.profileTile__imgRow, .profileTile__titleRow {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.profileTile__imgRow {
  width: 65px;
}

.profileTile__name {
  color: #5d5d5d;
  color: var(--mainText);
  padding: 2px 0;
}

.profileTile__title, .profileTile__date {
  font-style: italic;
  color: #a9a9a9;
  color: var(--subText);
  font-size: 12px;
  padding-top: 2px;
}

.profileTile__jon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
