:root {
  --sky: #caebf2;
  --carbon: #a9a9a9;
  --watermelon: #ff3b3f;
  --neutral: #efefef;
  --almostWhite: #fafafa;
  --fresh: #4abdac;
  --niceGrey: #edeeef;
  --darkGrey: #222222;
  --whiteFont: #dcdcdc;
  --hoverGrey: #3c3c3c;
  --font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Open Sans", "Helvetica Neue", "Ubuntu", sans-serif;

  --linkHover: #ffffff;

  --headerHeight: 50px;
  --footerHeight: 350px;
  --tabHeight: 27px;

  --backgroundOdd: var(--niceGrey);
  --backgroundEven: white;
  --backgroundEducation: lighten($darkGrey, 5%);
  /* TODO: redo the main text color */
  --mainText: #5d5d5d;
  --mainLighterText: #5d5d5d;
  --mainLightestText: #5d5d5d;
  --subText: var(--carbon);
  --phoneMinWidth: 320px;
  --phoneMaxWidth: 479px;
  --padMinWidth: 480px;
  --padMaxWidth: 767px;
  --desktopMinWidth: 768px;
}

/* Todo: fix postcss custom media and use this */

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
  width: 100%;
  background-color: #222222;
  background-color: var(--darkGrey);
  height: 350px;
  height: var(--footerHeight);
}

.footer h4 {
  color: #cfcfcf;
  text-align: center;
}

.footer__info {
  padding-top: 40px;
}

.footer__socialArea {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__socialArea a {
  margin: 0 3px;
}

.footer__about {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  padding-bottom: 30px;
}

.footer__resumeLink {
  padding: 0 15px;
  font-weight: bold;
  color: #a9a9a9;
  color: var(--carbon);
  text-decoration: none;
}

.footer__resumeLink:hover {
  color: #ffffff;
  color: var(--linkHover);
  cursor: pointer;
}

.footer__socialLink svg {
    height: 32px;
    width: 32px;
    color: #a9a9a9;
    color: var(--carbon)
}

.footer__socialLink svg:hover {
  color: #ffffff;
  color: var(--linkHover);
  cursor: pointer;
}
