:root {
  --sky: #caebf2;
  --carbon: #a9a9a9;
  --watermelon: #ff3b3f;
  --neutral: #efefef;
  --almostWhite: #fafafa;
  --fresh: #4abdac;
  --niceGrey: #edeeef;
  --darkGrey: #222222;
  --whiteFont: #dcdcdc;
  --hoverGrey: #3c3c3c;
  --font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Open Sans", "Helvetica Neue", "Ubuntu", sans-serif;

  --linkHover: #ffffff;

  --headerHeight: 50px;
  --footerHeight: 350px;
  --tabHeight: 27px;

  --backgroundOdd: var(--niceGrey);
  --backgroundEven: white;
  --backgroundEducation: lighten($darkGrey, 5%);
  /* TODO: redo the main text color */
  --mainText: #5d5d5d;
  --mainLighterText: #5d5d5d;
  --mainLightestText: #5d5d5d;
  --subText: var(--carbon);
  --phoneMinWidth: 320px;
  --phoneMaxWidth: 479px;
  --padMinWidth: 480px;
  --padMaxWidth: 767px;
  --desktopMinWidth: 768px;
}

/* Todo: fix postcss custom media and use this */

.blogEntry {
  display: flex;
  border: 1px solid #a9a9a9;
  border: 1px solid var(--carbon);
  background-color: white;
  background-color: var(--backgroundEven);
  border-radius: 20px;
  padding: 15px;
  margin-top: 20px;
  flex-direction: column;
  width: 600px;
  max-width: calc(100% - 50px);
}

.blogEntry__title {
  color: #5d5d5d;
  color: var(--mainText);
  font-size: 26px;
  font-weight: bold;
  padding: 10px 0;
}

.blogEntry__summary {
  color: var(--maintText);
}

.blogEntry__showMore {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: flex-end;
  color: #5d5d5d;
  color: var(--mainText);
  cursor: pointer;
  font-weight: bold;
}

.blogEntry__showMore:hover {
  color: #ff3b3f;
  color: var(--watermelon);
  fill: #ff3b3f;
  fill: var(--watermelon);
}

