:root {
  --sky: #caebf2;
  --carbon: #a9a9a9;
  --watermelon: #ff3b3f;
  --neutral: #efefef;
  --almostWhite: #fafafa;
  --fresh: #4abdac;
  --niceGrey: #edeeef;
  --darkGrey: #222222;
  --whiteFont: #dcdcdc;
  --hoverGrey: #3c3c3c;
  --font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Open Sans", "Helvetica Neue", "Ubuntu", sans-serif;

  --linkHover: #ffffff;

  --headerHeight: 50px;
  --footerHeight: 350px;
  --tabHeight: 27px;

  --backgroundOdd: var(--niceGrey);
  --backgroundEven: white;
  --backgroundEducation: lighten($darkGrey, 5%);
  /* TODO: redo the main text color */
  --mainText: #5d5d5d;
  --mainLighterText: #5d5d5d;
  --mainLightestText: #5d5d5d;
  --subText: var(--carbon);
  --phoneMinWidth: 320px;
  --phoneMaxWidth: 479px;
  --padMinWidth: 480px;
  --padMaxWidth: 767px;
  --desktopMinWidth: 768px;
}

/* Todo: fix postcss custom media and use this */

.blogBase {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-color: white;
  background-color: var(--backgroundEven);
}

.blogBase__inner {
  max-width: 1000px;
  padding: 32px 16px;
}

.blogBase svg {
  height: 48px;
  width: 48px;
}

.blogBase__backArea, .blogBase__nextArea {
  display: flex;
  align-items: center;
  cursor: pointer;
  color: #5d5d5d;
  color: var(--mainText);
}

.blogBase__backArea:hover, .blogBase__nextArea:hover {
  color: #ff3b3f;
  color: var(--watermelon);
}

.blogBase__backArea {
  margin: 0 0 10px var(--edgePadding);
}

.blogBase__nextArea {
  margin: 0 var(--edgePadding) 10px 0;
}

.blogBase__nextText {
  text-align: right;
}

.blogBase__title {
  font-size: 32px;
  font-weight: bold;
  padding: 20px 0;
}

.blogBase__titleArea, .blogBase__content {
  margin: 0 var(--edgePadding);
}

.blogBase__navigation {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 60px 0 30px 0;
}
