:root {
  --sky: #caebf2;
  --carbon: #a9a9a9;
  --watermelon: #ff3b3f;
  --neutral: #efefef;
  --almostWhite: #fafafa;
  --fresh: #4abdac;
  --niceGrey: #edeeef;
  --darkGrey: #222222;
  --whiteFont: #dcdcdc;
  --hoverGrey: #3c3c3c;
  --font: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Open Sans", "Helvetica Neue", "Ubuntu", sans-serif;

  --linkHover: #ffffff;

  --headerHeight: 50px;
  --footerHeight: 350px;
  --tabHeight: 27px;

  --backgroundOdd: var(--niceGrey);
  --backgroundEven: white;
  --backgroundEducation: lighten($darkGrey, 5%);
  /* TODO: redo the main text color */
  --mainText: #5d5d5d;
  --mainLighterText: #5d5d5d;
  --mainLightestText: #5d5d5d;
  --subText: var(--carbon);
  --phoneMinWidth: 320px;
  --phoneMaxWidth: 479px;
  --padMinWidth: 480px;
  --padMaxWidth: 767px;
  --desktopMinWidth: 768px;
}

/* Todo: fix postcss custom media and use this */

.profileArea {
  position: relative;
}

.profileArea > img {
  width: 100%;
}

.profileArea__jonwrapper {
  position: relative;
  width: 100%;
  height: 25vw;
}

.profileArea__jon {
  position: absolute;
  right: 0;
  left: 0;
  margin: 0 auto;
  border-radius: 50%;
  width: 25vw;
  border: 2px solid #efefef;
  border: 2px solid var(--neutral);
}

.profileArea__title {
  padding: 10px 0;
  font-size: 4vw;
  width: 100%;
  text-align: center;
  font-weight: 200;
}

.profileArea__title h1, .profileArea__title h2 {
  color: #edeeef;
  color: var(--niceGrey);
}

.profileArea__profile {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
  top: 0;
}
